@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
nav{padding:1em;background:#272727;grid-area:nav}nav h1{font-weight:300;color:#4bb2f9;font-size:42px}

.input-group{padding:0.5em;background:#121212;border:1px solid #121212;margin:4px;margin-top:8px;margin-bottom:8px;border-radius:8px;position:relative}.input-group:hover{border:1px solid #1c445f}.input-group .title{position:absolute;top:-9px;left:20px;padding:4px;background:#121212;border-radius:6px;border:1px solid #4bb3f9b6}.input-group input{display:block;border:none;background:none;color:white;font-size:18px;font-weight:300;padding-top:1em;width:100%}.input-group input:focus{outline:none}.input-group textarea{display:block;width:100%;border:none;background:none;color:white;font-size:18px;font-weight:300;padding-top:1em}.input-group textarea:focus{outline:none}

.general-area{color:#ffffffcc}.general-area h2{font-weight:300;margin-bottom:0.5em}.general-area form .row{display:flex}.general-area form .row .col{flex:1 1}

.experience-area{margin-top:1em;color:#ffffffcc}.experience-area h2{font-weight:300;margin-bottom:0.5em}.experience-area .experience-wrap{border:1px solid #1c445f;border-radius:8px;padding:0.5em;padding-top:1em;margin-bottom:0.5em}.experience-area .experience-wrap .delete-exp{display:flex;justify-content:flex-end}.experience-area .experience-wrap .delete-exp svg{stroke:#ff8080;stroke-width:0.7px;cursor:pointer}.experience-area .experience-wrap .delete-exp svg:hover{stroke:#c76363}.experience-area .experience-wrap .row{display:flex}.experience-area .experience-wrap .row .col{flex:1 1}.experience-area button{padding:0.5em 1em;border:1px solid #1c445f;background:none;color:white;font-size:18px;cursor:pointer;transition:all 0.15s;border-radius:8px}.experience-area button:hover{border:1px solid #4bb2f9}

.education-area{margin-top:1em;color:#ffffffcc}.education-area h2{font-weight:300;margin-bottom:0.5em}.education-area .education-wrap{border:1px solid #1c445f;border-radius:8px;padding:0.5em;padding-top:1em;margin-bottom:0.5em}.education-area .education-wrap .delete-exp{display:flex;justify-content:flex-end}.education-area .education-wrap .delete-exp svg{stroke:#ff8080;stroke-width:0.7px;cursor:pointer}.education-area .education-wrap .delete-exp svg:hover{stroke:#c76363}.education-area .education-wrap .row{display:flex}.education-area .education-wrap .row .col{flex:1 1}.education-area button{padding:0.5em 1em;border:1px solid #1c445f;background:none;color:white;font-size:18px;cursor:pointer;transition:all 0.15s;border-radius:8px}.education-area button:hover{border:1px solid #4bb2f9}

.edit-area{background:#272727;color:#ffffffcc;grid-area:edit;padding:1em;overflow-y:scroll}

.preview-area{grid-area:preview;background:#e2e2e2;color:#1c1c1c;display:grid;grid-gap:0;grid-template-columns:minmax(200px, 300px) 1fr;grid-template-rows:1fr}.preview-area .details{align-items:center;background:#1f446e;color:white;padding:1em}.preview-area .details span{display:block;width:100%;overflow-wrap:break-word}.preview-area .details .name{font-size:36px;font-weight:500}.preview-area .details .title{font-size:18px;font-weight:300}.preview-area .details .phone{font-size:18px;font-weight:300;margin-bottom:8px}.preview-area .details .email{font-size:18px;font-weight:300;margin-bottom:8px}.preview-area .details .linkedin{font-size:18px;font-weight:300;margin-bottom:8px;overflow:hidden;text-overflow:ellipsis}.preview-area .main-content{padding:1em}.preview-area .main-content .experience{padding:1em 0;border-bottom:1px solid black}.preview-area .main-content .experience .top{display:flex;justify-content:space-between;margin-bottom:10px}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif}html,body,#root,#root-wrap,main{height:100%}body{background:#121212;padding:1em}body #root-wrap{display:grid;grid-gap:1em;grid-template-columns:1fr 1fr;grid-template-rows:auto 1fr;grid-template-areas:"nav nav" "edit preview"}

